<template>
  <div class="personal-customer-detail main-container">
    <div class="base-info-rebate-container">
      <!-- 基础信息 -->
      <CompanyInfo></CompanyInfo>
      <!-- 跟进记录-->
      <FollowRecords></FollowRecords>
    </div>
    <div class="table-container">
      <TabList
        :list="tabList"
        @emitTab="tabClick"
        :activeIndex="initData.activeIndex"
      ></TabList>
      <component :is="initData.component"></component>
    </div>
  </div>
</template>
<script lang="ts">
export default {
  name: "customerManagement.personalCustomer.detail",
};
</script>
<script lang="ts" setup>
import { markRaw, shallowReactive, defineAsyncComponent } from "vue";

// 基本信息
const CompanyInfo = defineAsyncComponent(
  () =>
    import(
      "@components/customer-management/personal-customer/detail/company-info.vue"
    )
);
// 返佣明细
const FollowRecords = defineAsyncComponent(
  () =>
    import(
      "@components/customer-management/personal-customer/detail/follow-records.vue"
    )
);
// 订单管理
const OrderList = defineAsyncComponent(
  () =>
    import(
      "@components/customer-management/personal-customer/detail/order-list.vue"
    )
);
// 应用管理
const ApplicationList = defineAsyncComponent(
  () =>
    import(
      "@components/customer-management/personal-customer/detail/application-list.vue"
    )
);
// 服务管理
const ServiceList = defineAsyncComponent(
  () =>
    import(
      "@components/customer-management/personal-customer/detail/service-list.vue"
    )
);
const tabList = [
  {
    label: "订单管理",
    value: 0,
    component: markRaw(OrderList),
  },
  {
    label: "应用管理",
    value: 1,
    component: markRaw(ApplicationList),
  },
  {
    label: "服务管理",
    value: 2,
    component: markRaw(ServiceList),
  },
];
const initData = shallowReactive({
  activeIndex: 0,
  dateList: [],
  component: tabList[0].component,
});
// tab 点击
const tabClick = (item: Record<string, any>) => {
  initData.activeIndex = item.value;
  initData.component = item.component;
};
</script>
<style lang="scss" scoped>
.base-info-rebate-container {
  display: flex;
  height: 550px;
  margin-bottom: 20px;
}
.table-container {
  position: relative;
  padding: 32px 24px 20px;
}
</style>
